@import "_variables";
@import "helpers-placeholders";



 /**
  * -------------------------------------------------------------------------------
  * ( 04 ) - Content
  * -------------------------------------------------------------------------------
  *
  *  This part contains all content sections in main content
  *
  * ------
  *
  *  Content is divided to the following sections:
  *
  *    |
  *    |-->  [ 01 ] - Content
  *    |-->  [ 02 ] - Sidebar
  *    |-->  [ 03 ] - What We Do
  *    |-->  [ 04 ] - About Us
  *    |-->  [ 05 ] - Watch Video
  *    |-->  [ 06 ] - Our Services
  *    |-->  [ 07 ] - Our Projects
  *    |-->  [ 08 ] - Pricing Plans
  *    |-->  [ 09 ] - Our Blog
  *    |-->  [ 10 ] - Newsletter Subscribe
  *    |-->  [ 09 ] - Our Clients
  *    |-->  [ 10 ] - About Us
  *    |-->  [ 11 ] - Team Members
  *    |-->  [ 12 ] - Page Our Services
  *    |-->  [ 13 ] - Page Our Works
  *    |-->  [ 14 ] - Page Portfolio Single
  *    |-->  [ 15 ] - Page Our Blog
  *    |-->  [ 16 ] - Page Blog Single
  *    |-->  [ 17 ] - Page Jobs
  *    |-->  [ 18 ] - Page Project Order
  *    |-->  [ 19 ] - Page Send Suggestion
  *
  */


#content {
  position: relative;
  // overflow: hidden;
  line-height: $line-height-content;
  background-color: #fff;
  z-index: 5; /* Important for icons animations */

  h1,h2,h3,h4,h5,h6 {

  }
}

#content-wrap {
  position: relative;
}


/*  [ 01 ] - Content
====================== */

.content {
  &.sidebar-left {
    padding: 0 0 0 10px;
  }
}

.page-single {
  .section-content {
    padding-top: 0 !important;
    background-color: #fafafa;
  }

  .page-single-content {
    padding: 0 40px;
    box-shadow: 0 1px 4px rgba(#000, 0.15);
    background-color: #fff;

    .content {
      padding: 50px 0;
    }
  }

  .content {
    padding: 30px;
  }

  &.with-sidebar {
    .content {
      // padding: 50px 40px 10px 0;
      padding-right: 40px;
      border-right: 1px solid #ccc;
    }
  }

  // &:not(.with-sidebar) {
  //   .content {
  //     margin-bottom: -40px;
  //   }
  // }
}

@media (max-width: 992px) {
  .page-single {
    &.with-sidebar {
      .content {
        padding-right: 0;
        border-right: none;
      }
    }
  }
} // width < 992px



/*  [ 02 ] - Sidebar
====================== */

.sidebar {
  padding: 50px 0 50px 10px;
}

.box-widget {
  & + .box-widget {
    margin-top: 40px;
  }

  .box-title {
    margin-bottom: 20px;

    &::before {
      content: "";
      position: relative;
      top: 3px;
      display: inline-block;
      margin-left: 10px;
      width: 5px;
      height: 20px;
      background-color: var(--theme-color-1);
    }
  }
}


@media (max-width: 992px) {
  .sidebar {
    padding-top: 20px;
  }
} // width < 992px



/*  [ 03 ] - What We Do
========================= */

#what-we-do {
  .section-content {

  }
}

/* Box Service 1 */
.box-service-1 {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0;
  
  .box-icon {
    position: relative;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    float: none;
    margin: 0 35px 0 0;
    width: 70px;
    height: 70px;
    flex: 0 0 70px;
    border-radius: 999px;
    box-shadow: 0 0 0 1px $theme-color-1;
    background-color: #fff;
    transition: all 0.4s;

    i {
      position: relative;
      z-index: 1;
      font-size: 28px;
      line-height: 28px;
      color: $theme-color-1;
      transition: all 0.4s;
    }

    &::after {
      content: "";
      position: absolute;
      width: 86px;
      height: 86px;
      border-radius: 999px;
      border: 1px solid rgba($theme-color-1, 0.2);
      transition: all 0.4s;
    }

    &::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border-radius: 999px;
      opacity: 0;
      background-color: #fff;
      transition: all 0.3s;
    }
  }

  .box-content {
    position: relative;
    display: flex;
    flex-direction: column;
    padding-top: 20px;
  }

  &:hover {
    .box-icon {
      box-shadow: none;
      
      i {
        color: #fff;
      }
      
      &::before {
        width: 100%;
        height: 100%;
        opacity: 1;
        background-color: $theme-color-1;
      }

      &::after {
        border-color: rgba($theme-color-1, 1);
      }
    }
  }
}


/* Center Horizontal */
.center-horizontal {
  position: relative;
  display: inline-block;
  right: -50%;
  transform: translateX(-50%);
}

@media (max-width: 480px) {
  .xs-center-horizontal {
    position: relative;
    display: inline-block;
    left: -50%;
    transform: translateX(50%);
  }
} // < 480px width


/* List Info */
.list-info {
  list-style: none;
  display: flex;
  flex-direction: column;
  font-size: 25px;
  font-weight: 600;
  color: $heading-color;

  @media (max-width: 1400px) {
    font-size: 22px;
  } // < 1400px width

  &.medium {
    font-size: 20px;

    li {
      &::before {
        top: 8px;
        height: 20px;
        flex: 0 0 20px;
        background-size: cover !important;
      }
    }
  }

  &.grey {
    color: $body-color;
  }

  @media (max-width: 480px) {
    font-size: 17px;
  } // < 480px width

  li {
    display: flex;

    & + li {
      margin-top: 5px;
    }
    
    &::before {
      content: "";
      position: relative;
      top: 10px;
      margin-left: 15px;
      width: 27px;
      height: 27px;
      flex: 0 0 27px;

      @media (max-width: 1400px) {
        top: 8px;
      } // < 1400px width
      
      @media (max-width: 480px) {
        top: 2px;
      } // < 480px width
    }
  }

  &.list-1 {
    li {
      &::before {
        background: url(../images/files/list-bullet-1.png) center no-repeat;
      }
    }
  }

  &.list-2 {
    li {
      &::before {
        background: url(../images/files/list-bullet-2.png) center no-repeat;
      }
    }
  }

}


/*  [ 04 ] - About Us
=========================== */

#about-us {
  background-color: #f6f6f6;

  .section-content {

  }

  .img-featured {
    width: 475px;
    height: 450px;

