/*  [ 05 ] - Watch Video
=========================== */

#watch-video {
  .section-content {
    padding-bottom: 180px;
  }
}

/* Btn Video */
.btn-video {
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
  width: 70px;
  height: 70px;
  font-size: 20px;
  text-indent: 2px;
  border-radius: 999px;
  background-color: #fff;
  color: $theme-color-1;

  &::before,
  &::after {
    content: "";
    position: absolute;
    border-radius: 999px;
    transition: all 0.2s;
  }

    &::before {
    width: 100px;
    height: 100px;
    background-color: rgba(#fff, 0.1);
  }

  &::after {
    width: 130px;
    height: 130px;
    border: 1px solid rgba(#fff, 0.2);
  }
  
  &:hover {
    color: #fff;
    background-color: $theme-color-1;

    &::before {
      width: 90px;
      height: 90px;
      background-color: rgba(#fff, 0.12);
    }

    &::after {
      width: 110px;
      height: 110px;
      border-color: rgba(#fff, 0.3);
    }
  }
}





/*  [ 06 ] - Our Services
=========================== */

#our-services {  
  .section-content {
    padding-top: 0;
  }
}

/* Box Service 2 */
.box-service-2 {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 55px 50px;
  border-radius: 5px;
  box-shadow: 0px 15px 36px 4px rgba(#000, 0.05);
  transition: all 0.2s;
  background-color: #fff;
  
