    transition: all 0.2s;
  }

  h6 {
    opacity: 0.4;
    font-weight: 500;
  }

  h4 {
    margin-bottom: 20px;

    &:hover a {
      opacity: 0.8;
    }
  }

  .btn-rm {
    margin-top: 10px;
  }

  &:hover {
    .box-icon {
      box-shadow: none;
      
      i {
        color: #fff;
      }
      
      &::before {
        width: 100%;
        height: 100%;
        opacity: 1;
        background-color: $theme-color-1;
      }

      &::after {
        border-color: rgba($theme-color-1, 1);
      }
    }
  }

  &:hover {
    transform: translateY(-10px);
    background-color: $theme-color-1;

    h6,
