}

/* Slider Testimonials */
.slider-testimonials {
  margin-top: 80px;

  .slick-slider {
    .slick-list {
      margin: 0 -15px; // 15px padding + 15px of slick-slide
    }
    
    .slick-slide {
      padding: 0 200px;
      transition: all 0.4s;

      @media (max-width: 992px) {
        padding: 0 15px;
      } // < 992px width
    }
  }

  .slick-arrows {
    font-size: 16px;
    margin: 40px 0 0;

    a {
      position: relative;
      color: $dark-color;

      &.slick-next:hover:not(.slick-disabled) {
        left: 1px;
      }
      
      &.slick-prev:hover:not(.slick-disabled) {
        right: 1px;
      }

      &:hover {
        color: $theme-color-1;
      }

      &.slick-disabled { // disabled arrow
        opacity: 0.5;

        &:hover {
          color: $dark-color;
        }
      }
    }
  }
}



/* Box Preview */
.box-preview {
  width: 100%;
  position: relative;
  top: 0;
  transition: all 0.3s ease;

  &:hover {
    .box-img {  
      .overlay {
        // overlay & overlay-inner hover state visibility
        &,
        .overlay-inner {
          opacity: 1;
          visibility: visible;          
        }

        // overlay-inner show delay when hidden
        .overlay-inner {
          transition-delay: 0.25s;
        }
      }
    }
  }

  .box-img {
    position: relative;
    border-radius: 0;
    transition: all 0.3s ease;

    .overlay {
      position: absolute;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      background-color: rgba(#000,0.75);
      transition: all 0.3s ease;

      // overlay & overlay-inner default state visibility
      &,
      .overlay-inner {
        opacity: 0;
        visibility: hidden;
      }

      .overlay-inner {
        transition: all 0.3s ease;
      }
    }
  }

  .box-content {
    padding: 45px 25px;
    position: relative;
  }
}

/* Box Preview Work */
.box-preview-work {
  overflow: hidden;
  position: relative;
  display: flex;
  z-index: 0;
  border-radius: 10px;
  box-shadow: 0px 4px 21px 0 rgba(#000, 0.15);
  transition: all 0.2s;
  background-color: #f9f9f9;

  @media (max-width: 1200px) {
    flex-direction: column;
  } // < 1200px width
  
  .box-img {
    overflow: hidden;
    margin: 0 0 0 30px;
    // padding: 30px;
    height: auto;
    min-height: 200px;
    // width: 230px;
    flex: 0 0 550px;
    // border-radius: 4px;
    background-color: #fff;

    @media (max-width: 1200px) {
      margin: 0;
      flex: 0 0 100%;
      height: 400px;
    } // < 1200px width

    @media (max-width: 992px) {
      height: 300px;
    } // < 992px width
    
    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      transition: all 0.2s;

      img {
        max-height: 100%;
        width: auto;
      }
    }

    .overlay {
      transition-duration: 0.2s;
      color: #bbb; // content color inside overlay
      background-color: rgba(#fff, 0);

      .overlay-inner {
        align-items: center;
        justify-content: center;
      }

      // overlay & overlay-inner default visibility
      &,
      .overlay-inner {
        opacity: 0;
        visibility: hidden;
      }
    }

    // hover box img
    &:hover {
      .overlay {
        &,
        .overlay-inner {

        }
      }
    }
  }

  .box-content {
    display: flex;
    flex-direction: column;
    padding: 80px 40px 80px 100px;

    @media (max-width: 768px) {
      padding-left: 40px;
    } // < 768px width

    @media (max-width: 480px) {
      padding-top: 50px;
    } // < 480px width
    
    h3 {
      margin-bottom: 20px;

      a {
        color: $dark-color;

        &:hover {
          color: $theme-color-1;
        }

        @at-root .theme-dark & {
          color: #fff;

          &:hover {
            color: $theme-color-1;
          }
        }
      }
    }

    p {
      font-size: 20px;
      color: $body-color;

      @media (max-width: 480px) {
        font-size: 16px;
      } // < 480px width
    }

    .btn-rm {
      margin: 40px auto 0 0;
      text-decoration: underline;

      @media (max-width: 1200px) {
        margin-top: 0;
      } // < 1200px width
    }
  }

  // hover box preview
  &:hover {    
    .box-img {
      > .bg-element {
        // opacity: 0.6;
      }
    }

    .overlay {
      background-color: rgba(#fff, 0.2);
      
      &,
      .overlay-inner {
        opacity: 1;
        visibility: visible;
      }

      .overlay-inner {
        transition-delay: 0.2s;
      }
    }

    .box-content {
      
    }
  }
}

