          transition-delay: 0.2s;
        }
      }

      .box-content {

      }
    }
  }

  .icon-fullscreen {
    margin-bottom: 10px;
    font-size: 35px;
    color: #fff;

    &:hover {
      opacity: 0.6;
    }
  }

  h4 {
    margin-bottom: 5px;
    
    a {
      color: #fff;

      &:hover {
        opacity: 0.7;
      }
    }
  }
  
  .project-categories {
    list-style: none;
    display: flex;

    li {
      & + li {
