  [class*="col-"] {
    // padding: 0 2.5px;
  }

  .pt-column {
    position: relative;
    top: 0;
    padding: 55px 50px;
    border: 1px dashed $theme-color-1;
    border-radius: 5px;
    transition: all 0.3s;
    
    &.featured {
      border: none;
      background-color: $theme-color-1;

      .pt-column-header {
        border-bottom-color: rgba(#fff, 0.5);
        
        .title,
        .price h1 {
          color: #fff;
        }
      }

      .pt-column-content {
        color: #fff;

        i {
          color: #fff;
        }
      }
    }

    &:hover {
      transform: translateY(-15px);
    }
  }

  .pt-column:hover {

  }

  /* Column Header */
  .pt-column-header {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    padding-bottom: 25px;
    margin-bottom: 22px;
    border-bottom: 1px solid #eee;
    font-family: $heading-font;

    .title {
      font-size: 18px;
      font-weight: 500;
      color: $theme-color-1;
    }

    .price {
      display: flex;
      align-items: flex-end;
      
      h1 {
        margin-bottom: 0;
        font-size: 70px;
        color: $theme-color-1;

        @media (max-width: 992px) {
          font-size: 60px;
        } // < 992px width

        @media (max-width: 480px) {
          font-size: 50px;
        } // < 480px width
      }
    }
  }

  /* Column Content */
  .pt-column-content {
    list-style: none;
    margin-bottom: 40px;
    font-size: 16px;
    text-align: center;

    li {
      position: relative;
      display: flex;
      align-items: center;
      margin-bottom: 1px;

      & + li {
        margin-top: 10px;
      }

      i {
        margin-right: 10px;
        color: $dark-color;
      }
    }
  }

  /* Column Footer */
  .pt-column-footer {

  }
}


/* Box Blog Post */
.box-blog-post {
  position: relative;
  display: flex;
  flex-direction: column;
  z-index: 0;
  height: 100%;
  transition: all 0.2s;

  .box-img {
    overflow: hidden;
    margin: 0;
    // padding: 30px;
    height: auto;
    min-height: 250px;
    width: 100%;
    border-radius: 5px;
    background-color: #fff;

    @media (max-width: 1400px) {
      min-height: 230px;
    } // < 1400px width

    > a {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      transition: all 0.2s;

      img {
        display: none;
        max-height: 100%;
        width: auto;
      }
    }

    .overlay {
      transition-duration: 0.3s;
      color: #bbb; // content color inside overlay
      background-color: rgba(#fff, 0);

      .overlay-inner {
        align-items: center;
        justify-content: center;
      }

      // overlay & overlay-inner default visibility
      &,
      .overlay-inner {
        opacity: 0;
        visibility: hidden;
      }
    }

    // hover box img
    &:hover {
      .overlay {
        &,
        .overlay-inner {

        }
      }
    }
  }

  .box-content {
    display: flex;
    flex-direction: column;
    padding: 25px 0 0;
    flex: 1 0 auto;
    // background-color: #fff;

    .list-meta {
      margin-bottom: 10px;
    }

    h4 {
      // margin: 10px 0 8px;

      a {
        color: $dark-color;

        &:hover {
          color: $theme-color-1;
        }
      }
    }
  }

  // hover box preview
  &:hover {    
    .box-img {
      > .bg-element {
        // opacity: 0.6;
      }
    }

    .overlay {
      background-color: rgba($dark-color, 0.6);
      
      &,
      .overlay-inner {
        opacity: 1;
        visibility: visible;
      }

      .overlay-inner {
        transition-delay: 0.2s;
      }
    }

    .box-content {
      
    }
  }
}

/* List Meta */
.list-meta {
  list-style: none;
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 500;
  font-family: $heading-font;

  li {
    display: flex;
    align-items: center;
    
    & + li {
      margin-left: 20px;
    }
  }

  i {
    margin-right: 6px;
    // font-size: 18px;
    color: $dark-color;
  }
}


/* Slider Blog Posts */
.slider-blog-posts {
  .slick-slider {
    overflow: hidden;
    
    .slick-list {
      margin: 0 -15px;
    }
    
    .slick-slide {
      padding: 0 15px;
      transition: all 0.4s;

      &:not(.slick-active) {
        opacity: 0;
      }
    }
  }
}


