.color-bg-white { background-color: #fff; }
.color-grey { color: #666; }
.color-bg-grey { background-color: #666; }
.color-red { color: $color-red; }
.color-green { color: $color-green; }
.color-bg-gradient-1 { @include bgGradient($theme-color-1, darken($theme-color-2, 15%), 270deg); }

/* Font Sizes */
.font-size {
	&-10 { font-size: 10px; }
	&-11 { font-size: 11px; }
	&-12 { font-size: 12px; }
	&-13 { font-size: 13px; }
	&-14 { font-size: 14px; }
	&-15 { font-size: 15px; }
	&-16 { font-size: 16px; }
	&-17 { font-size: 17px; }
	&-18 { font-size: 18px; }
	&-19 { font-size: 19px; }
	&-20 { font-size: 20px; }
	&-22 { 
		font-size: 22px;

		@media (max-width: 1400px) {
		  font-size: 18px;
		} // < 1400px width

		@media (max-width: 480px) {
		  font-size: 14px;
		} // < 480px width
	}
}

/* Text Alignment */
@media (max-width: 992px) {
	.md-text-center {
		text-align: center;
	}

	.md-text-right {
		text-align: right;
	}

	.md-text-left {
		text-align: left;
	}
} // < 992px width

@media (max-width: 768px) {
	.sm-text-center {
		text-align: center;
	}

	.sm-text-right {
		text-align: right;
	}

	.sm-text-left {
		text-align: left;
	}
} // < 768px width
	
@media (max-width: 480px) {
	.xs-text-center {
		text-align: center;
	}

	.xs-text-right {
		text-align: right;
	}

	.xs-text-left {
		text-align: left;
	}
} // < 480px width

@media (max-width: 320px) {
	.md-text-center {
		text-align: center;
	}

	.md-text-right {
