@import "_variables";


/**
 *  -------------------------------------------------------------------------------
 *  ( 05 ) - Footer
 *  -------------------------------------------------------------------------------
 *
 *  This part contains footer and mini footer
 *
 *  ------
 *
 *  It has the following code:
 *
 *    |
 *    |-->  [ 01 ] - Footer
 *    |-->  [ 02 ] - Footer Bars
 
 */




/*  [ 01 ] - Footer
===================== */

#footer {
  overflow: hidden;
  position: relative;
  z-index: 1;
}


/*  [ 02 ] - Footer Bars
========================== */

/* Footer Bar */
.footer-bar {
  padding: 100px 0;
  background: transparent;

  & + .footer-bar {
    // border-top: 1px solid $theme-color-4;
  }

  .fb-row {
    // display: flex;
    // align-items: flex-start;

    & + .fb-row {
      padding-top: 50px;
      margin-top: 50px;
      border-top: 1px solid rgba($dark-color,0.05);
    }
  }
}

@media (max-width: 768px) {
  .footer-bar {
    .fb-row {
      flex-direction: column;
    }
  }
} // < 768px width


.footer-bar-wrap {

}

.footer-bar {
  .container,
  .row,
  [class*="col-"] {
    // height: 100%;
  }
}


/* Footer Bar 1 */
#footer-bar-1 {
  display: flex;
  align-items: center;
  background-color: #222;
  
  .footer-bar-wrap {
    width: 100%;
  }

  &.footer-bar {
    .fb-row {
      h4 {
        display: flex;
        align-items: center;
        margin-bottom: 25px;
        font-size: 18px;
        text-transform: uppercase;
      }
    }
  }

  &.text-white {

  }

  h5 {
    margin-bottom: 20px;
  }
}

/* List Contact Info */
.list-contact-info {
  list-style: none;
  color: #fff;
  font-size: 16px;

  li {
    display: flex;
    align-items: flex-start;

    & + li {
      margin-top: 15px;
    }
  }

  i {
    position: relative;
    top: 4px;
    margin-right: 20px;
    font-size: 18px;
    opacity: 0.7;
    color: #fff;
  }

  h5 {
    margin-bottom: 5px !important;
  }

  .info-content {
    span {
      opacity: 0.7;
    }
  }
}

/* Footer Widget Row */
.footer-widget-row {
  & + .footer-widget-row {
    margin-top: 40px;
  }
}

/* List Links */
.list-links {
  list-style: none;

  li {
    & + li {
      margin-top: 10px;
    }

    &:first-child a {
      padding-top: 0;
    }

    a {
      display: flex;
      padding-top: 3px;
      font-size: 14px;
      opacity: 0.7;
      color: $dark-color;

      &::before {
        content: "\f077";
        font-family: "Font Awesome 5 Free";
        font-weight: 700;
        display: inline-block;
        position: relative;
        top: -1px;
        margin-right: 12px;
        font-size: 10px;
        color: #fff;
        transition: all 0.15s;
        transform: rotate(90deg);
      }

      &:hover {
        opacity: 1;

        &::before {
          margin-right: 15px;
        }
      }
    }
  }

  .text-white & {
    li {
      a {
        color: #fff;
      }
    }
  }
}


/* List Gallery Thumbs */
.list-gallery-thumbs {
  list-style: none;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin: 0 -7.5px -7.5px;

  > li {
    padding: 0 7.5px;
    margin-bottom: 15px;
    width: 33.33333%;

    @media (max-width: 992px) {
      width: 80px;
    } // < 992px width
  }

  a {
    display: block;

    img {
      width: 100%;
    }

    &:hover {
      opacity: 0.6;
    }
  }
}

/* Footer Bar 2 */
#footer-bar-2 {
  padding: 0;
  background-color: #222;
  
  .footer-bar-wrap {
    width: 100%;
  }

  &.footer-bar {
    .fb-row {
      display: flex;
      align-items: center;
      padding: 20px 0;
      border-top: 1px solid rgba(#fff, 0.1);

      @media (max-width: 768px) {
        flex-direction: column;
        justify-content: center;
        padding: 30px 0;
      } // < 768px width
    }
  }

  .social-icons {
    margin: 0 0 -5px auto;

    @media (max-width: 768px) {
      margin-left: 0;
      margin-top: 10px;
    } // < 768px width
  }
}


/* Copyrights Message */
.copyrights-message {
  font-size: 16px;
  font-weight: 500;

  > * {
    opacity: 0.7;
  }

  a {
    opacity: 1;
    font-weight: 700;
    color: #fff;
    
    &:hover {
      text-decoration: underline;
    }
  }

  .text-white & {
    color: #fff;
  }
}


  

