@import "_variables";


 /**
  * -------------------------------------------------------------------------------
  * ( 04 ) - Banner
  * -------------------------------------------------------------------------------
  *
  *  This part contains all banner related contents like banner slider, bannar
  *  parallax
  *
  * ------
  *
  *  Banner is divided to the following sections:
  *
  *    |
  *    |-->  [ 01 ] - Banner Parallax
  *    |-->  [ 02 ] - Banner Slider
  *
  */


#banner {
  position: relative;
  // overflow: hidden;
  background-color: #fff;

  &.fullscreen {
    .banner-parallax,
    .banner-parallax .row > [class*="col-"] {
      min-height: auto; // fix needed due to flexbox use.
    }
  }
}



/*  [ 01 ] - Banner Parallax
============================== */

.banner-parallax {
  // overflow: hidden;
  // height: 800px;
  position: relative;
  background-color: #fff;
  background-position: top center;
  background-size: cover;
  // background-attachment: fixed;
  background-repeat: no-repeat;

  &.blured-bg {
    > .bg-element {
      -webkit-filter: blur(5px);
              filter: blur(5px);      
    }
  }

  > .bg-element {
    position: absolute;
    width: 100%;
    height: 100%;
    background: no-repeat top center / cover;
    background-size: contain;
    // opacity: 0;
    transition: opacity 0.5s;

    &.appeared {
      opacity: 1;
    }

    & + img {
      display: none;
    }
  }

  > .overlay-pattern {
    opacity: 0;
    transition: opacity 0.5s;

    &.appeared {
      opacity: 1;
    }
  }

  // -------
  // some fix
  &,
  .row > [class*="col-"] {
    min-height: 800px; // banner parallax min height

    @media (max-width: 1400px) {
      height: auto !important;
      min-height: auto !important;
    } // < 1400px width
  }

  .row {
    display: flex;
    align-items: center;
  }

  .row > [class*="col-"] {
    display: flex;
    // justify-content: center;
    align-items: center;
  }
  // -------

  .container,
  .row,
  .row > [class*="col-"] {
    height: 100%;
    z-index: 1;
  }

  .container {
    // width: 100%;
  }
}


.overlay-colored,
.overlay-pattern {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.overlay-colored {
  z-index: 1;
}


.overlay-pattern {
  z-index: 2;
  background-image: url("../images/general-elements/overlay-pattern.png");
  // background-color: rgba(0,0,0,0.3);
  background-repeat: repeat;
  background-position: top left;
}


@media ( min-width: 992px ) {
  .fullscreen {
    .banner-parallax,
    .banner-slider {
      height: 100% !important;
    }
  }
}

.video-background {
  position: absolute;
  width: 100%;
  height: 100%;
}

.owl-carousel .owl-item { // Fix for slider overlay content
  -webkit-backface-visibility: initial;
          backface-visibility: initial;
}

/* Slide Content */
.slide-content {
  height: 100%;
  width: 100%;
  position: relative;
  top: 0;
  z-index: 2;

  @media (max-width: 480px) {
    text-align: center;
  } // < 480px width

  img {
    display: inline-block !important;
  }

  h5 {
    margin-bottom: 20px;
    font-weight: 500;
    opacity: 0.8;
  }

  h1 {
    position: relative;
    font-size: 60px;
    line-height: 1.2;

    @media (max-width: 1400px) {
      font-size: 50px;
    } // < 1400px width

    @media (max-width: 480px) {
      font-size: 35px;
    } // < 480px width
  }
  
  p {
    font-size: 20px;
    color: #fff;
  }
  

  .text-center {
    h1,
    h4 {
      margin-left: auto;
      margin-right: auto;
      // width: 75%;
    }

    h1 {
      width: 85%;
    }
  }

  // optimizing h1 & .description width for .cta-subscribe-3
  .col-md-10 .text-center {
    h1 {
      // width: 70%;
    }

    .description {
      // width: 70%;
    }
  }

  .text-right {
    h1 {
      margin-left: auto;
    }

    .description {
      margin-left: auto;
    }
  }
}

@media (max-width: 992px) {
  .slide-content {
    [class*="text-center"] {
      h1,
      h4 {
        width: 100%;
      }

      .description {
        margin-left: auto;
        margin-right: auto;
        width: 80%;
      }
    }

    .col-md-10 .text-center {
      h1,
      .description {
        width: 100%;
      }
    }
  }
} // < 992px width

@media (max-width: 768px) {
  .slide-content {
    h1 {
      // font-size: 45px;
    }
  }
} // < 768px width
  

@media (max-width: 480px) {
  .slide-content {
    h1 {
      // font-size: 40px;
    }

    h1,
    p,
    .description {
      width: 100%;
