/* Banner Center Box */
.banner-center-box {
  padding: 220px 0 160px;
  max-height: 100%;
  max-width: 100%;
  width: 100%;
  perspective: 1000px;
  backface-visibility: hidden;

  // when there is #header in page
  #header + #banner & {
    // padding-top: 160px;
  }

  > * {
    position: relative;
    // top: 0;
    // transition: none;
  }

  .text-center {

  }

  &.text-white {
    h1,
    .description {
      color: #fff;
    }
  }
}

.banner-center-box.bottom-position {
  margin-top: auto; // this for making the flex item at bottom
  padding-bottom: 0;
}

.banner-center-box h1:last-child,
.banner-center-box h2:last-child,
.banner-center-box h3:last-child,
.banner-center-box h4:last-child,
.banner-center-box h5:last-child,
.banner-center-box h6:last-child {
  padding-bottom: 0;
  margin-bottom: 0;
}

.banner-center-box img {
  max-height: 100%;
  max-width: 100%;
}


@media (max-width: 992px) {

  /* Column With Helper Classes Optimization */
  [class*="col-"] {
    &[class*="pl-"] {
      padding-left: 15px;
    }

    &[class*="pr-"] {
      padding-right: 15px;
    }
  }

  /* Banner Parallax */
  .banner-parallax {
    // text-align: center;

    &,
    .row > [class*="col-"] {
      height: auto !important;
      min-height: auto !important;
    }

    .row {
      flex-direction: column;
      // min-height: 600px; // min-height of banner parallax
    }

    .row > [class*="col-"] {
      align-self: stretch;
      flex-grow: 1;
    }

    [class*="col-"]:not(:first-child) .banner-center-box {
      padding-top: 0 !important;
      margin-top: -80px;
    }
  }

  .slide-content,
  .banner-center-box {
    // position: initial;
  }

  .banner-center-box {
    display: block;
    top: 0;
    width: 100%;
    // padding-top: 180px; // padding-bottom + header height
    transform: translateY(0);
  }

  /* Slider */
  .fullscreen {
    height: auto !important;
  }

  .banner-slider {
    min-height: auto !important;
  }
} // < 992px width

@media (max-width: 768px) {
  .banner-parallax {
    .row {
      // min-height: 500px; // min-height of banner parallax
    }
  }
} // < 768px width


/* Slick Default Settings */
.slick-slider {
    // in case we want to clip content to the grid sides
    // overflow: hidden;
  
  .slick-list {
    margin-left: -15px;
    margin-right: -15px;
  }

  .slick-slide {
    position: relative;
    padding-left: 15px;
    padding-right: 15px;
    outline: none;
  }
}

/* Slick Dots */
.slick-dotted .slick-dots {
  list-style: none;
  position: relative;
  z-index: 1;
  display: flex !important;
  align-items: center;
  justify-content: center;
  margin-top: 40px;
  font-size: 0;
  
  li {
    & + li {
      margin-left: 10px;
    }

    button { // all dots except active
      display: flex;
      align-items: center;
      justify-content: center;
      border: none;
      outline: none;
      width: 12px;
      height: 12px;
      border-radius: 999px;
      transition: all 0.2s;
      background-color: #aaa;
    }
    
    &.slick-active { // active dot
      button {
        background-color: $theme-color-1;
      }
    }
  }
}

/* Slick Arrows */
.slick-arrows {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  font-size: 30px;

  a {
    margin: 0 5px;

    &:hover {
      color: #000;
    }

    &.slick-disabled { // disabled arrow
      opacity: 0.5;

      &:hover {
        color: $theme-color-1;
      }
    }
  }

  i {
    display: block;
  }
}


/* Slider Banner */
.slider-banner {
  position: relative;

  .slide {
    width: 100%;
  }

  a {
    img {
      // width: 100%;

      @media (max-width: 1400px) {
        max-height: 400px;
      } // < 1400px width
    }
  }

  .slick-slide {
    display: flex;
    justify-content: flex-end;
  }

  .slick-arrows {
    justify-content: flex-end;
    padding-left: 120px;
  }

  .slick-dots {
    position: absolute;
    bottom: 50px;
    margin-top: 0;
    width: 100%;
  }
} // .slider-banner end

